.element-item {
 position: relative;
 float: left;
}

.element-item > * {
 margin: 0;
 padding: 0;
}

#sorts {
 font-size: 1em;
}

#sorts ul {
 list-style-type: none;
 display: inline-block;
}

#sorts li {
 margin: 0;
 padding: 0;
 margin-right: 0.5em;
 background: transparent;
 display: inline-block;
}

#sorts li:hover {
 color: red;
 text-decoration: underline;
}

#sorts li:selected {
 color: red;
 text-decoration: underline;
}

.project-filter-wrap {
}

.projects-sort .span4 {
 float: right;
 text-align: right;
}

/* ---- END ISOTOPE GRID STYLING ---- */

// Styling for NEXT-PREV project snippets. Not currently in use
.more-items {
 width: 100%;
 position: relative;
 padding: 1em;

 .prev-project {
 }

 .next-project {
 position: relative;
 right: 0;
 bottom: 0;
 width: 30em;
 }

 .nextprevtext {
 position: absolute;
 top: 1em;
 left: 1em;
 width: inherit;

 p {
 color: white !important;
 text-transform: none;
 font-weight: bold;
 font-size: 3em;
 overflow: wrap;
 }
 }
}

/* Responsive
----------------------------------------------
----------------------------------------------
----------------------------------------------
---------------------------------------------- */
@media only screen and (max-width: 1200px) {
}
@media only screen and (max-width: 1024px) {
 html {
 font-size: 16px;
 }

 .span3 {
 width: 33.33%;
 }

 .span4 {
 width: 50%;
 }
}
@media only screen and (max-width: 767px) {
 html {
 font-size: 14px;
 }

 .content {
 padding: 0 $l-space;
 }

 .row {
 padding: 0;
 }

 .span4 {
 width: 50%;
 }

 .span3 {
 width: 50%;
 }

 .small {
 line-height: 1.3;
 margin-bottom: 1em;
 font-size: 0.9em;
 }

 .project-details .span4 {
 width: 50%;
 }
 /* shows navigation */
 nav.expanded #navigation {
 }

 .three-col {
 column-count: 2;
 }

 .project-filter-wrap {
 padding-top: 4em;
 }

 .projects-sort .span4 {
 float: left;
 text-align: left;
 margin-bottom: 2em;
 }
}
@media only screen and (max-width: 480px) {
 html {
 font-size: 12px;
 }

 .content {
 padding: 0 $m-space;
 }

 [class*="span"] {
 width: 100%;
 }

 footer {
 .span3 {
 width: 50%;
 }

 p {
 font-size: 1.1em;
 }

 ul {
 margin: 0 auto;
 }

 li {
 padding: 0 $s-space;
 }
 }

 h1 {
 text-align: left;
 font-size: 2em;
 }

 hr {
 height: 4px;
 border-top: 4px solid $bodycolor;
 margin: 0 0 $m-space;
 }

 #navigation {
 margin: 0;
 padding-top: $xl-space;

 .span12 {
 margin-bottom: $m-space;
 }

 h1 {
 margin: $m-space 0 $l-space;
 font-size: 2.5em;
 text-align: center;
 width: 100%;
 }
 }
 /* expanded menu on mobile*/
 .submenu ul {
 list-style: none;
 margin: 0;
 text-align: center;
 }

 .submenu li {
 display: block;
 margin: 0 0 $s-space;
 line-height: 1.6;
 width: 100%;
 float: left;
 }
 /* Hide the Page navigation at certain scale*/
 .project-grid-item {
 h2,
 h3 {
 opacity: 1;
 }
 }

 .wide-header-space {
 margin-top: 60px;
 }

 .wide-header {
 min-height: 400px;
 }

 .wide-header:after {
 padding-bottom: 40%;
 }

 .two-col {
 column-count: 1;
 }

 h2 {
 font-size: 2.2em;
 margin-bottom: $m-space;
 }

 .fixed-square-wrapper {
 width: 100%;
 position: relative;
 padding-bottom: 0;
 }

 .fixed-text {
 position: relative;
 transform: translateY(0);
 }
}
